
import {BsFacebook,BsTwitter} from "react-icons/bs"
import { Link , useNavigate} from "react-router-dom";
import axios from "axios";
import { useEffect, useState } from "react";

function Signup() {

  const navigate=useNavigate()
const [name,setName]=useState()
const [username,setUsername]=useState()
const [mobile,setMobile]=useState()
const [password,setPassword]=useState()

const [loginMsg,setLoginMsg]=useState()
const [otp,setOtp]=useState(0)
var otpp;


  const submitHandler=(e)=>{
      console.log(otp);
      console.log(Number(e.target.otp.value));

    if(otp==Number(e.target.otp.value)){
      console.log(e.target.name.value);
      console.log(e.target.email.value);
      console.log(e.target.mobile.value);
      console.log(e.target.password.value);


      axios.post("https://demo.thefacts.space/api/user/signup",{
        name:e.target.name.value,
        mobile:e.target.mobile.value,
        email:e.target.email.value,
        password:e.target.password.value
      }).then((res)=>{
          if(res.data.msg==="signup success"){
            console.log("success");
            localStorage.setItem("loggedIn","true")
            navigate("/home")
          }

          setLoginMsg(res.data.msg)
      })

    }

      e.preventDefault();
  }

  const otpHandler=()=>{
    const form=document.querySelector("form")
    if(form.email.value!==""){
      otpp=  Math.floor(Math.random()*10000)


       axios.post("https://demo.thefacts.space/api/mailer/otp",{
        email:form.email.value,
        otp:otpp
       }).then((res)=>{
        setLoginMsg(res.data.msg)
        setOtp(otpp)
        document.querySelector(".otp-msg").style.color="green"

       })

    
      
       document.querySelector(".btn-submit").style.display="block"
       document.querySelector(".btn-otp").style.display="none"
       document.querySelector(".otp-box").style.display="block"
      }
  }


  useEffect(()=>{
    async function loginCheck(){
      if( localStorage.getItem("loggedIn")==="true"){
        navigate("/home")
      }

    }
    loginCheck()
    document.querySelector(".otp-box").style.display="none"
    document.querySelector(".btn-submit").style.display="none"
  },[])


  return (
    <div className="signup">
      <div className="signup-container container">
        {/* <div className="left">
        <img className="login-banner" src="https://images.pexels.com/photos/2582937/pexels-photo-2582937.jpeg?auto=compress&cs=tinysrgb&w=600" alt="" />
        </div> */}
        <div className="right">
        
         
         <div className="row-container space-bw">
           <span className="title">Sign Up</span>
           <div className="icon-container">
             <div className="icon">
             <BsFacebook color="grey" size="1.25em"/>

             </div>
             <div className="icon">

             <BsTwitter color="grey" size="1.25em"/>
             </div>


           </div>


         </div>
         <br />
         <h3 className="otp-msg">{loginMsg!==undefined?loginMsg:""}</h3>
             <form  onSubmit={submitHandler}>
               <br/>
               <label htmlFor="name">Name</label><br/>
               <input id="name" name="name" type="text" placeholder="Enter Name" required/><br/>

               <label htmlFor="username">Username</label><br/>
               <input id="username" name="email" type="email" placeholder="Enter Username" required/><br/>
               
               <label htmlFor="mobile">Mobile</label><br/>
               <input id="mobile" name="mobile" type="text" placeholder="Enter number" required/><br/>

               <label htmlFor="password">Password</label><br/>
               <input id="password" name="password" type="password" placeholder="Enter Password" required/>
               
               <div className="otp-box">
               <label htmlFor="otp">OTP</label><br/>
               <input id="otp" name="otp" type="number" placeholder="Enter OTP" required/>
               </div>

             <div className="div row-container space-bw">
               <div className="form-control">
                 <input type="checkbox" name="" id="remember" />
               <label htmlFor="remember">I agree to terms and conditions</label>
               </div>
               
             </div>
               
               <button  className="btn btn-warning btn-submit">sign Up</button>
               
             </form>
               <button onClick={otpHandler} className="btn btn-warning btn-otp">Send OTP</button>

             <div className="container">
               <center>

               <p className="">Already have an accout?<Link to="/login"><b className="bold-color-text">Signin</b></Link> </p>
               </center>
             </div>
       
        </div>
      </div>
     
    </div>
  );
}

export default Signup;
